import React, { FC, Fragment, ReactElement } from "react";
import { MdAttachFile, MdCardMembership, MdNoteAdd } from "react-icons/md";
import {
  DocumentUser,
  DocumentSubCategories,
  DocumentsCategories,
  DocumentStatus,
} from "../../interfaces";
type DocumentsListProps = {
  documents: DocumentUser[];
  selected_category: DocumentsCategories | null;
  selected_sub_category: DocumentSubCategories | null;
  selectDoc: Function;
  addNewDoc: Function;
  categories: DocumentsCategories[];
  sub_categories: DocumentSubCategories[];
};

const DocumentsList: FC<DocumentsListProps> = ({
  documents,
  selected_category,
  selected_sub_category,
  selectDoc,
  addNewDoc,
  categories,
  sub_categories,
}): ReactElement => {
  let subCategoriesByCategory: DocumentSubCategories[] = sub_categories.filter(
    (sub_cat: DocumentSubCategories) =>
      sub_cat.category_id === selected_category?.user_detail_category_id
  );
  let documents_list: DocumentUser[] = [];
  if (selected_category === null) {
    // * no category selected
    documents_list = documents;
  } else {
    //* nothing is selected in subcategory (default)
    if (selected_sub_category === null) {
      // check if the category has sub categories
      documents_list = documents.filter(
        (doc: DocumentUser) =>
          doc.category_id === selected_category?.user_detail_category_id
      );
    } else if (selected_sub_category !== null) {
      documents_list = documents.filter(
        (doc: DocumentUser) =>
          doc.category_id === selected_category.user_detail_category_id &&
          doc.sub_category_id ===
            selected_sub_category.user_detail_subcategory_id
      );
    }
  }
  return (
    <div className="">
      <div className="p-2 border-b border-gray-200 rounded-md flex bg-white justify-between items-center">
        <section>
          <h1
            className={`${
              selected_sub_category === null ? "text-2xl" : "text-xl"
            }  m-0  text-blue-800`}
          >
            {selected_category === null ? (
              "All details"
            ) : (
              <Fragment>
                {selected_category?.title}{" "}
                {selected_sub_category !== null && (
                  <b className="block -mt-1"> {selected_sub_category.title}</b>
                )}
              </Fragment>
            )}
          </h1>
          <p className="m-0 text-sm text-gray-400">
            {selected_sub_category === null
              ? selected_category?.description
              : selected_sub_category.description}
          </p>
        </section>

        {(selected_sub_category === null &&
          subCategoriesByCategory.length === 0) ||
        !(
          selected_sub_category === null && subCategoriesByCategory.length >= 1
        ) ? (
          <button
            className="px-3 py-1 bg-blue-600 rounded hover:bg-blue-800 text-white"
            onClick={() => addNewDoc()}
          >
            <MdNoteAdd className="text-3xl mx-auto block" />
            <span className="text-sm">Add new</span>
          </button>
        ) : selected_sub_category === null &&
          subCategoriesByCategory.length >= 1 ? (
          <section className="text-right">
            <button
              className="px-3 py-1 inline-block bg-gray-300 rounded text-gray-700"
              disabled
            >
              <MdNoteAdd className="text-2xl mx-auto block" />
              <span className="text-xs">Add new</span>
            </button>
            <span className="text-xs block">Select a sub cateogry</span>
          </section>
        ) : null}
      </div>
      <div className="p-2 ">
        {documents_list.length <= 0 ? (
          <div className="p-4 text-center  text-gray-500">
            <MdCardMembership className="text-7xl mx-auto my-2" />
            <h2 className="text-2xl mb-2">No data found on</h2>
            <h1 className="text-xl mb-2 text-blue-900">
              {selected_category === null
                ? "Any category"
                : selected_category?.title}{" "}
              {selected_sub_category !== null && (
                <b className="block"> {selected_sub_category?.title} </b>
              )}
            </h1>
            <p>
              Please click on <b>Add new</b> and improve your CV
            </p>
          </div>
        ) : (
          documents_list.map((doc: DocumentUser, i) => {
            let category_itm = categories.find(
              (itm) => itm.user_detail_category_id === doc.category_id
            );
            let sub_category_itm = sub_categories.find(
              (itm) => itm.user_detail_subcategory_id === doc.sub_category_id
            );

            let colorBackground: string = "";
            let colorBorder: string = "";

            if (doc.validation_status === DocumentStatus.Approved) {
              colorBackground = "white";
              colorBorder = "green-500 ";
            } else if (doc.validation_status === DocumentStatus.Waiting) {
              colorBackground = "white";
              colorBorder = "blue-500 ";
            } else if (doc.validation_status === DocumentStatus.Rejected) {
              colorBackground = "red-100";
              colorBorder = "red-500 ";
            }

            return (
              <div
                key={i}
                className={`flex px-0.5 py-2 cursor-pointer mb-2 rounded shadow-sm-- hover:shadow border hover:bg-blue-100  border-${colorBorder}-100  items-center bg-${colorBackground}`}
                onClick={() => selectDoc(doc)}
              >
                <div className="flex-1 pl-2">
                  <span className="text-sm block text-gray-600">
                    {category_itm?.title}
                  </span>
                  <div className="  text-lg m-0  text-gray-900">
                    <h3 className="font-bold inline-block">
                      {sub_category_itm?.title}
                      {doc.title && " - " + doc.title}
                    </h3>

                    <h3 className="inline-block ml-2">
                      {doc.category_subcategory_data?.title}
                    </h3>
                  </div>
                  {doc.validation_status !== undefined &&
                    doc?.files?.length >= 4 && (
                      <span
                        className={`text-sm  bg-${colorBorder}-500 py-0.5 px-2 rounded-full text-white`}
                      >
                        {doc?.validation_status}
                      </span>
                    )}
                </div>
                <div className=" mx-2">
                  {doc.files !== undefined && doc.files?.length >= 4 && (
                    <button className="p-2" title="This item has a file">
                      <MdAttachFile
                        className={`text-4xl text-${colorBorder}-400`}
                      />
                    </button>
                  )}
                </div>
              </div>
            );
          })
        )}
      </div>
    </div>
  );
};
export default DocumentsList;
