import React, { FC, ReactElement } from "react";
import {
  CandidateInterface,
  DocumentsCategories,
  DocumentSubCategories,
  DocumentUser,
} from "../../interfaces";
import { EDUCATION_CATEGORY_ID } from "../../utils/app.config";

type PrintCVProps = {
  user: CandidateInterface;
  documentTypes: {
    sub_categories: DocumentSubCategories[];
    categories: DocumentsCategories[];
  };
  documents: DocumentUser[];
};

const PrintCV: FC<PrintCVProps> = ({
  user,
  documentTypes,
  documents,
}): ReactElement => {
  const { categories, sub_categories } = documentTypes;

  if (documents.length <= 0) {
    return (
      <div className="p-4 text-center">
        <h1 className="text-3xl font-bold text-gray-500">
          You have no data to generate the CV
        </h1>
        <h3>Please complete the details in order to get your profile</h3>
      </div>
    );
  }

  return (
    <div>
      <div id="content" className="p-3 border" style={{ width: "2480" }}>
        <div className="py-1"></div>
        <div className=" ">
          <div className="flex">
            <div className="">
              <img
                src="https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png"
                alt="Profile details"
                className="w-28"
              />
            </div>
            <div className="flex-1 pl-3">
              <h1 className="text-3xl font-bold">
                {user.fname} {user.lname}
              </h1>
              <section className="border-t my-1">
                <span className="text-gray-500 inlinr-block mr-2">Sex:</span>
                <b>{user.sex}</b>
              </section>
              <section className="border-t my-1">
                <span className="text-gray-500 inlinr-block mr-2">Email:</span>
                <b>{user.email}</b>
              </section>
              <section className="border-t my-1">
                <span className="text-gray-500 inlinr-block mr-2">Phone:</span>
                <b>{user.phone_number}</b>
              </section>
            </div>
          </div>
        </div>
        <div className="mt-2 border-t grid grid-flow-col gap-4">
          <div className="">
            <h1 className="p-1 mb-1 bg-gray-600 text-center text-lg font-bold text-white">
              Education
            </h1>
            {sub_categories
              .filter(
                (sub_cat_check) =>
                  sub_cat_check.category_id === EDUCATION_CATEGORY_ID
              )
              .map((sub_cat) => {
                let documents_list = documents.filter(
                  (doc) =>
                    doc.sub_category_id === sub_cat.user_detail_subcategory_id
                );
                if (documents_list.length <= 0) return null;

                return (
                  <div
                    key={sub_cat.user_detail_subcategory_id}
                    className="border p-2 rounded mb-2"
                  >
                    <h1 className="text-xl text-blue-500 font-bold">
                      {sub_cat.title}
                    </h1>
                    <div className="mt-1">
                      {documents_list.map((doc) => (
                        <div key={doc.doc_id} className="p-1 border-t">
                          <b>
                            {doc.category_subcategory_data
                              ? doc.category_subcategory_data.title
                              : "Document"}
                          </b>
                          <p>{doc.school?.school_name}</p>
                        </div>
                      ))}
                    </div>
                  </div>
                );
              })}
          </div>

          <div className="">
            {categories
              .filter(
                (cat) => cat.user_detail_category_id !== EDUCATION_CATEGORY_ID
              )
              .map((category) => {
                let sub_categories_list = sub_categories.filter(
                  (itm) => itm.category_id === category.user_detail_category_id
                );
                if (sub_categories_list.length <= 0) return null;

                return (
                  <section key={category.user_detail_category_id}>
                    <h1 className="p-1 mb-1 bg-gray-600 text-center text-xl font-bold text-white">
                      {category.title}
                    </h1>
                    {sub_categories_list.map((sub_cat) => {
                      let documents_list = documents.filter(
                        (doc) =>
                          doc.sub_category_id ===
                          sub_cat.user_detail_subcategory_id
                      );
                      if (documents_list.length <= 0) return null;

                      return (
                        <div
                          key={sub_cat.user_detail_subcategory_id}
                          className="border p-2 rounded mb-2"
                        >
                          <h1 className="text-xl text-blue-500 font-bold">
                            {sub_cat.title}
                          </h1>
                          <div className="mt-1">
                            {documents_list.map((doc) => (
                              <div key={doc.doc_id} className="p-1 border-t">
                                {doc.title.length >= 2 && <b>{doc.title} </b>}-{" "}
                                <b>{sub_cat.title}</b> -{" "}
                                <b>
                                  {doc.category_subcategory_data
                                    ? doc.category_subcategory_data.title
                                    : "Document"}
                                </b>
                                <p>{doc.school?.school_name}</p>
                              </div>
                            ))}
                          </div>
                        </div>
                      );
                    })}
                  </section>
                );
              })}
          </div>
        </div>
      </div>
    </div>
  );
};
export default PrintCV;
